.page-photo__main {
  font-family: $font-family;
  color: $text-color;
}

.page-photo {
  background-color: #2a3949;
}

.page-photo__wrapper {
  width: 320px;
  margin: 0 auto;  
}

.page-photo__picture {
  display: flex;
  justify-content: center;  
  
  height: 237px;

  background-image: url("./../img/bg-back-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: 0 0;
}

.page-photo__title {
  @include margin0-padding0;
  
  padding: 0 20px;
  padding-top: 96px;
  
  color: $white-color;
  font-size: 24px;
  font-weight: 300;
  text-align: center;
}

@media (min-width: $tablet-width) {
  .page-photo__wrapper {
    width: 100%;  
  }

  .page-photo__picture {
    height: 440px;

    background-image: url("./../img/bg-back-tablet@1x.jpg");
    background-size: cover;
  }
  
  .page-photo__title {
    width: 620px;
    margin: 0 auto;
    padding-top: 180px;    

    font-size: 50px; 
  }
}

@media (min-width: $desktop-width) {
  .page-photo__wrapper {
    width: 100%; 
  }

  .page-photo__picture {    

    background-image: url("./../img/bg-back-desktop@1x.jpg");
    background-size: cover;
  }

  .page-photo__title {    
    width: 940px;
    margin: 0 auto;
    padding-top: 180px;    

    font-size: 50px; 
  }
}
