.advantages {
  text-align: center;
  color: $text-color;
  background-color: $white-color;
}

.advantages__header-text-wrapper {
  padding-top: 40px;
  padding-bottom: 30px;
}

.advantages__header-text {
  @include margin0-padding0;
  width: 280px;
  margin: 0 auto;
  margin-bottom: 30px;

  font-size: 24px;
  font-weight: 300;
  line-height: 36px;
}

.advantages__list-block {
  position: relative; 
  padding-top: 80px;
  background-color: $grey-bg;
  
  &:after {
    content: '';
    position: absolute;

    top: 0px;
    left: 0;

    width: 100%;
    height: 100%;

    background-image: url("./../img/bg-triangle-white.svg");
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: 100%;
  }
}

.advantages__list {
  width: 280px;
  margin: 0 auto;
  margin-bottom: 40px;
}

.advantages__item {
  margin-bottom: 30px;
  text-align: center;
}

.advantages__desktop-picture {
  display: none;
}

.advantages__title {
  @include margin0-padding0;

  margin-bottom: 15px;

  font-size: 18px;
  font-weight: 700;
  line-height: 24px;

  color: $black-color;
}

.advantages__description {
  @include margin0-padding0;
 
  color: $text-color;

  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
}

.advantages__picture {
  @include margin0-padding0;

  display: flex;
  justify-content: center;
  width: 280px;
  margin: 0 auto;
  overflow: hidden;
}

.advantages__image {
  margin-bottom: -42px;
  display: block;
}

.advantages__effect-list {
  display: none;
}

@media (min-width: $tablet-width) {
  .advantages__header-text-wrapper {
    padding-top: 50px;
    padding-bottom: 40px;
  }

  .advantages__header-text {
    margin-bottom: 10px;
    width: 620px;
    font-size: 40px;
    font-weight: 300;    
  }

  .advantages__list-block {
    padding: 0;
  }

  .advantages__list-wrapper {
    display: flex;
    justify-content: space-between;

    position: relative;
    z-index: 2;

    padding-top: 0;
    width: 620px;
    margin: 0 auto;
  }

  .advantages__list {
    @include margin0-padding0;

    margin-top: 70px;
    margin-bottom: 0;
    width: 310px;

    order: 2;
  }

  .advantages__item {
    text-align: start;
  }

  .advantages__picture {
    @include margin0-padding0;
    position: relative; 
    justify-content: flex-start;
    z-index: 2;
  }

  .advantages__effect-list {
    display: block;
    padding-top: 59px;
  }

  .advantages__effect-wrapper {
    display: flex;
    justify-content: space-between;

    width: 620px;
    margin: 0 auto;
  }

  .advantages__effect-icon {
    position: relative;

    &:after {
      content: "";
      position: absolute;

      top: 56px;
      left: 18px;

      width: 176px;
      height: 176px;
      border-radius: 50%;

      border: 13px solid $red-color;
      
    }
  }

  .advantages__effect-number {
    position: absolute;

    top: 85px;
    left: 64px;

    font-size: 96px;
    font-weight: 700;
    color: $red-color;
  }

  .advantages__effect-image {
    width: 238px;
    height: auto;
  }

  .advantages__effect-item {
    width: 310px;
  }

  .advantages__effect-title {
    @include margin0-padding0;

    margin-top: 39px;
    margin-bottom: 18px;    

    font-size: 30px;
    font-weight: 700;
    line-height: 36px;
    text-align: left;
  }

  .advantages__effect-title span{
    display: block;
  }

  .advantages__effect-description {
    @include margin0-padding0;

    margin-bottom: 14px;

    font-size: 14px;
    font-weight: 400;
    line-height: 24px;
    text-align: left;
  }

  .advantages__effect-description-link {
    display: block;
    font-size: 14px;
    font-weight: 400;
    line-height: 24px;
    text-align: left;
    color: $red-color;
  }
}

@media (min-width: $desktop-width) {
  .advantages__header-text-wrapper {
    padding-top: 75px;
    padding-bottom: 70px;
  }

  .advantages__picture {
    display: none;
  }

  .advantages__list-wrapper {
    margin: 0 auto;
    width: 940px;
  }

  .advantages__list { 
    justify-content: space-between;
    width: 100%; 
    margin-top: 0;
    display: flex;
    
  }

  .advantages__desktop-picture {
    @include margin0-padding0;

    display: block;
  }

  .advantages__desktop-image {
    display: block;
    margin: 0 auto;
  }

  .advantages__item {
    width: 300px;
  }

  .advantages__title {
    margin-top: 56px;
    margin-bottom: 22px;
    font-size: 30px;
    line-height: 30px;
    text-align: center;
  }

  .advantages__description {
    padding: 0 30px;
    font-size: 18px;
    line-height: 30px;
    text-align: center;
  }

  .advantages__effect-list {    
    padding-bottom: 67px;
  }

  .advantages__effect-wrapper {
    width: 940px;
  }

  .advantages__effect-icon {    

    &:after {
      
      top: 70px;
      left: 21px;

      width: 220px;
      height: 220px;

      border: 17px solid $red-color;      
    }
  }

  .advantages__effect-number {
    top: 115px;
    left: 80px;

    font-size: 120px;
  }
  
  .advantages__effect-list {
    background-color: $grey-bg;
  }

  .advantages__effect-title {
    margin-top: 98px;
    margin-bottom: 22px;
  }

  .advantages__effect-title span{
    display: inline-block;
  }

  .advantages__effect-description {
    margin-bottom: 32px;
  }

  .advantages__effect-image {
    width: 298px;
    height: auto;
  }

  .advantages__effect-item {
    width: 570px;
  }

  .advantages__effect-description {
    padding-right: 49px;

    font-size: 18px;
    line-height: 30px;
  }

  .advantages__effect-description-link {
    font-size: 18px;
    line-height: 30px;
  }
}
