.reviews__appeal {
  box-sizing: border-box;
  width: 280px;
  margin: 0 auto;
  padding: 50px 15px;
  font-size: 14px;
  line-height: 30px;
  font-weight: 400;
  text-align: center;
}

.reviews__panorama-item {
  @include margin0-padding0;
  
  margin-bottom: 20px;

  display: flex;
  flex-direction: column;
}

.reviews__panorama-picture {
  position: relative;
  display: block;
  width: 320px;
  margin: 0 auto;  
}

.reviews__label {  
  @include margin0-padding0;
  position: absolute;

  right: 20px;
  top: 0;

  padding: 8px 20px;
  font-size: 12px;
  line-height: 24px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;

  color: $white-color;

  background-color: $red-color;
}

.reviews__panorama-image img {
  display: block;
}

.reviews__panorama-wrapper {
  box-sizing: border-box;
  width: 280px;
  margin: 0 auto;  
  padding: 22px 20px 26px 15px;

  background-color: $grey-bg;
}

.reviews__author-name {
  display: block;

  font-size: 14px;
  line-height: 24px;
  font-weight: 700;
  font-style: normal;

  color: $text-color;
  text-decoration: none;
}

.reviews__author-name span {
  font-size: 12px;
  font-weight: 300;
  text-transform: uppercase;
}

.reviews__panorama-text {
  @include margin0-padding0;

  margin-bottom: 18px;

  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
  color: $text-color;
  white-space: normal;
}

.reviews__panorama-likes {
  display: flex;
  align-items: center;
}

.review__like-icon {
  margin-right: 18px;
  width: 13px;
  height: 12px;

  background-image: url("./../img/icon-heart.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: 100%;
}

.reviews__list { 
  width: 280px;
  margin: 0 auto;  
}

.reviews__item {
  @include margin0-padding0;

  margin-bottom: 20px;
  background-color: $grey-bg;
}

.reviews__custom-image img{
  display: block;
}

.reviews__text-wrapper {
  box-sizing: border-box;  
  padding: 22px 20px 26px 15px;

  background-color: $grey-bg;
}


.reviews__text {
  @include margin0-padding0;

  margin-bottom: 18px;

  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
  color: $text-color;
  white-space: normal;
}

.reviews__likes {
  display: flex;
  align-items: center;
}

.review__like-icon {
  font-size: 12px;
  line-height: 24px;
  font-weight: 400;
  color: $red-color;
}

.review__panorama-like,
.review__like {
  @include margin0-padding0;

  font-size: 12px;
  line-height: 24px;
  font-weight: 400;
  color: $red-color;
}

.review__likes {
  margin-top: auto;
}

@media (min-width: $tablet-width) {
  .reviews__appeal {
    width: 620px;
    margin: 0 auto;
    padding: 98px 85px 78px;
    font-size: 18px;
    line-height: 30px;
    font-weight: 400;
  }

  .reviews__panorama-item {    
    margin-bottom: 50px;
  }

  .reviews__panorama-picture {
    width: 660px;    
    overflow: hidden;
  }

  .reviews__label {
    top: 57px;
  }

  .reviews__panorama-wrapper {
    display: flex;
    flex-direction: column;
    width: 620px;
    padding: 28px;
  }

  .reviews__panorama-text {
    order: 2;
  }

  .reviews__panorama-likes {
    display: flex;
    align-self: flex-end;

    margin-top: -25px;
  }

  .review__like-icon {
    margin-right: 10px;
    order: 2;
  }

  .review__panorama-like {
    margin-right: 15px;
  }

  .reviews__list {
    width: 620px;
    margin: 0 auto;
  }

  .reviews__list-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin-left: -20px;
  }

  .reviews__item {
    margin-left: 20px;
    width: 300px;
  }  

  .reviews__custom-picture {
    display: block;
    margin: 0;
    padding: 0;
  }

  .reviews__custom-image {
    width: 300px;
    height: 300px;
  }
  
  .reviews__text-wrapper {
    display: flex;
    flex-direction: column;
    
    padding: 28px;   
  }

  .reviews__likes {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: auto;
  }
}

@media (min-width: $desktop-width) {
  .reviews__appeal {
    width: 940px;
    margin: 0 auto;
    padding: 98px 180px 90px;
  }

  .reviews__panorama-picture {
    width: 1200px;
    overflow: hidden;
    margin: 0 auto;
  }

  .reviews__label {
    top: 57px;
    right: 140px;
  }

  .reviews__panorama-wrapper {
    margin: 0 auto;
    width: 940px;
  }

  .reviews__list {
    width: 940px;        
  }

  .reviews__custom-image {
    width: 300px;
    height: 400px;
  }
}
