.contacts__wrapper {
  width: 280px;
  margin: 0 auto;
}

.contacts__title {
  @include margin0-padding0;

  font-size: 24px;
  line-height: 30px;
  font-weight: 300;
}

.contacts__text {
  font-size: 18px;
}

.contacts__link {
  @include margin0-padding0;

  font-size: 24px;
  line-height: 30px;
  font-weight: 300;
  color: $red-color;
}

.contacts__link-tel {
  @include margin0-padding0;

  font-size: 24px;
  line-height: 30px;
  font-weight: 300;
  color: $red-color;
  text-decoration: none;
}

@media (min-width: $tablet-width) {
  .contacts__wrapper {
    width: 620px;
    margin: 0 auto;
  }
}

@media (min-width: $desktop-width) {
  .contacts__wrapper {
    width: 940px;
    margin: 0 auto;
  }
}


