.page-footer {
  padding-top: 40px;
  padding-bottom: 40px;
}

.index-page {
  display: none;
}

.page-footer__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;

  width: 320px;
  margin: 0 auto;
}

.page-footer__logo {
  margin-bottom: 40px;
}

.page-footer__logo-image {
  display: none;
}

.page-footer__social {
  margin-bottom: 40px;
}

.page-footer__social-list {
  @include margin0-padding0;

  display: flex;  

  list-style: none;
}

.page-footer__social-item {
  margin-right: 12px;

  &:last-child {
    margin-right: 0; 
  }
}

.page-footer__social-link {
  display: flex;
  justify-content: center;
  align-items: center;
  
  width: 46px;
  height: 46px;

  border-radius: 50%;
  border: 3px solid $text-color-10;

  &:hover {
    border: 3px solid $text-color;
  }

  &:active {
    border: 3px solid $text-color-10;
  }

  &:active path {
    fill: $red-color-30;
  }
}

.social__link--youtube {
  padding-left: 5px;
}

.copyright__text {
  @include margin0-padding0;

  font-family: $font-family;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;

  color: $text-color;
}

.page-footer__copyright {
  display: flex;
  justify-content: center;
  align-items: center;
}

.copyright__link {
  @include margin0-padding0;
  display: block;
  font-size: 0;
}

.copyright__link-icon {
  margin-left: 14px;

  &:hover path {
    fill: $text-color;
  }

  &:active path {
    opacity: 0.3;
  }
}

.page-footer__logo--desktop {
    display: none;
  }

@media (min-width: $tablet-width) {
  .index-page {
    display: flex;
  }

  .page-footer {
    padding-top: 36px;
    padding-bottom: 36px;
  }

  .page-footer__wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 620px;
    margin: 0 auto;
  }

  .page-footer__logo {
    margin-bottom: 0;
  }

  .page-footer__logo--tablet {

    &:hover path {
      fill: $red-color;
    }

    &:active path {
      opacity: 0.3;
    }
  }

  .page-footer__logo--desktop {
    display: none;
  }

  .page-footer__social {
    margin-bottom: 0;
  }
}

@media (min-width: $desktop-width) {
  .page-footer {
    margin: 0 auto;
    justify-content: space-between;

    padding-top: 70px;
    padding-bottom: 70px;
  }

  .page-footer__wrapper {
    width: 940px;
    margin: 0 auto;
  }

  .page-footer__logo--tablet {
    display: none;
  }

  .page-footer__logo--desktop {
    display: block;

    &:hover path {
      fill: $red-color;
    }

    &:active path {
      opacity: 0.3;
    }
  }  
}
