.map__wrapper {
  display: flex;
  justify-content: center;
  
  position: relative;
  width: 320px;
  margin: 0 auto;
  overflow: hidden;
  height: 200px;
}

.map__image {
  position: absolute;
  display: block;
  
  bottom: 0;
  left: 0;
}


@media (min-width: $tablet-width) {
  .map__wrapper {
    width: 620px;
    overflow: hidden;
    height: 374px;
  }
}

