.image-processing {
  background-color: $grey-bg;
}

.image-processing__container {
  margin-bottom: 42px;
}

.image-processing__container {
  width: 280px;
  margin: 0 auto;
  padding: 20px 20px 0;
  padding-bottom: 42px; 
}

.image-processing__process-list {
  @include margin0-padding0;

  display: flex;
  justify-content: space-around;
  align-items: center;

  list-style: none;
  width: 280px;
  margin: 0 auto;
  padding: 0 20px;
  padding-bottom: 34px;  
}

.image-processing__item {
  cursor: pointer;
}

.image-processing__image img {
  display: block;
}

.image-processing__item-icon path {
  fill: $text-color-30;
}

.image-processing__item-icon rect {
  fill: $text-color-30;
}

.image-processing__item:hover  path,
.image-processing__item:focus  path,
.image-processing__item:hover  rect,
.image-processing__item:focus  rect {
  fill: $text-color-50;
}

.image-processing--active path {
  fill: $red-color;
}

.image-processing__item:hover  .image-processing--active path,
.image-processing__item:focus  .image-processing--active path {
  fill: $red-color;
}

.image-processing__control {
  width: 320px;
  margin: 0 auto;
  padding-bottom: 28px;  
  border-bottom: 1px solid $text-color-10;
}

.control__bar {
  margin: 0 auto;
  width: 240px;
  height: 2px;
  background-color: $text-color;
}

.control__toggle {
  position: relative;
  margin: 0 auto;

  &:after {
    content: "";
    position: absolute;

    top: -8px;
    left: 193px;

    width: 10px;
    height: 10px;

    background-color: $grey-bg;
    border: 2px solid $red-color;
    border-radius: 50%;
  }
}

.image-processing__send {
  display: flex;
  flex-direction: column;
  width: 280px;
  margin: 0 auto;
  padding: 0 20px;
  padding-top: 38px;
  padding-bottom: 41px;  
}

.button {
  padding: 15px 88px;

  font-family: $font-family;
  font-size: 18px;
  border: none;
  background-color: transparent;

  cursor: pointer;
}

.button__upload {
  margin-bottom: 12px;
  line-height: 18px;
  font-weight: 700;

  border: 3px solid $red-color;
  color: $red-color;
  border-radius: 50px;
}

.button__upload:hover,
.button__upload:focus {
  border: 3px solid $red-color;
  color: $white-color;
  background-color: $red-color;
}

.button__upload:active {
  color: $white-color-30;
}

.button__cansel {
  line-height: 18px;
  font-weight: 700;
  border-radius: 50px;

  border: 3px solid $text-color-10;
  color: $text-color-10;
}

.button__cansel:hover,
.button__cansel:focus {  
  color: $text-color-30;  
  border: 3px solid $text-color-30;
}

.button__cansel:active {  
  color: $white-color-30;
  background-color: $text-color-30;
}

.control {
  display: none;
}

@media (min-width: $tablet-width) {
  .image-processing__wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 620px;
    margin: 0 auto;
    padding-top: 57px; 
    padding-bottom: 54px;   
  }

  .image-processing__container {
    box-sizing: border-box;
    width: 300px;
    margin: 0;
    padding: 0;        
  }

  .image-processing__controls-wrapper {    
    width: 300px;
    margin: 0;
    margin-top: -18px;
  }

  .image-processing__process-list {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    width: 300px;
    margin: 0;
    padding: 0;
    padding-left: 25px;
  }

  .image-processing__item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-self: stretch;

    border-bottom: 1px solid $text-color-10;

    cursor: default;
  }

  .icon-crop {
    width: 22px;
    height: auto;
  }

  .icon-fill {
    width: 24px;
    height: auto;
  }

  .icon-contrast {
    width: 16px;
    height: auto;
  }

  .image-processing__item-icon path,
  .image-processing__item-icon rect {
    fill: $red-color;
  }

  .image-processing__control {
    @include margin0-padding0;

    margin-top: 36px;
    margin-left: 18px;
    margin-bottom: 34px;
    margin-right: 18px;   
    width: 202px;
    border-bottom: none;
  }

  .control {
    display: block;
  }

  .control__bar {
    width: 100%;
  }

  .control__toggle:after {
      left: 146px;
  }  

  .mobile-control {
    display: none;
  }

  .image-processing__send {
    padding: 0;   
    padding-top: 67px;
    padding-bottom: 0;
    padding-left: 25px;
  }

  .button__upload {
    margin-bottom: 24px;
  }
}

@media (min-width: $desktop-width) {
  .image-processing {
    background-color: $white-color;
  }

  .image-processing__wrapper {
    box-sizing: border-box;
    width: 940px;
    margin: 0 auto;
    padding: 57px 61px 57px 81px;

    background-color: $grey-bg;
  }

  .image-processing__controls-wrapper {
    display: flex;
    flex-direction: column;
    width: 412px;
  }

  .image-processing__control {
    margin-right: 30px;
    width: 288px;
  }

  .image-processing__process-list {
    padding-left: 0;
    width: 100%
  }
  
  .icon-crop {
    width: 44px;
    height: auto;
  }

  .icon-fill {
    width: 48px;
    height: auto;
  }

  .icon-contrast {
    width: 32px;
    height: auto;
  }

  .image-processing__send {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    
    margin-top: auto;
    padding: 0;
  }
  
  .button__upload {
    width: 183px;
    margin-bottom: 0;
    padding: 16px 0 18px 0;
  }

  .button__cansel {
    width: 183px;
    padding: 16px 0 18px 0;
  }
}
