.comments__wrapper {
  display: flex;
  width: 280px;
  margin: 0 auto;
}

.comments__list {
  padding: 20px;
}

.comments__author-name {
  display: block;
  font-size: 14px;
  line-height: 24px;
  font-weight: 700;
  font-style: normal;
  color: $text-color;
  text-decoration: none;
}

.comments__text {
  @include margin0-padding0;

  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
  color: $text-color;
  white-space: normal;
}

@media (min-width: $tablet-width) {
  .comments__wrapper {  
    width: 620px;    
  }

  .comments__list {

    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    max-width: 620px;

    margin: 50px 20px;
  }

  .comments__item {
    width: 300px;
  }
}

@media (min-width: $desktop-width) {
  .comments__wrapper {  
    width: 940px;    
  }
  
  .comments__list {
    max-width: 940px;
    margin: 0 auto;
    margin-top: 50px;
  }
}



