.contest__title {
  @include margin0-padding0;

  font-size: 14px;
  line-height: 30px;
  font-weight: 400;
}

.contest__data-list {
  @include margin0-padding0;
  border: none;
}

.contest__data-item {
  @include margin0-padding0;
}

.contest__data-label {
  font-size: 14px;
  line-height: 30px;
  font-weight: 400;

  color: $red-color;
  text-transform: uppercase;
}

.contest__achievements-item {
  @include margin0-padding0;
}

.contest__data-input {
  @include margin0-padding0;

  padding: 10px 20px;
  font-family: $font-family;

  @include font18-30-400;

  border: 2px solid $grey-border;
}

.contest__achievements-list {
  @include margin0-padding0;
  border: none;

  background-color: $grey-bg;
}

.contest__achievements-title {
  @include margin0-padding0;

  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: $red-color;
  text-transform: uppercase;
}

.contest__achievements-label {
  @include margin0-padding0;
  @include font18-30-400;
}

.contest__apps-item {
  @include margin0-padding0;
}

.contest__apps-label {
  @include margin0-padding0;
  @include font18-30-400;
}

.contest__emo-area {
  @include margin0-padding0;
  border: none;
}

.contest__emo-area-title {
  font-size: 14px;
  line-height: 30px;
  font-weight: 400;
  color: $red-color;
  text-transform: uppercase;
}

.contest__emo-area-item {
  font-family: $font-family;
  font-size: 18px;
  line-height: 30px;
  font-weight: 400;

  border: 2px solid $grey-border;
}

.contest__submit {
  font-size: 18px;
  line-height: 30px;
  font-weight: 700;
  color: $white-color;
  text-transform: uppercase;

  background-color: $green-color;
  border: 2px solid $green-color;
}

.contest__submit:hover {
  background-color: $green-color-dark;
  border: 2px solid $green-color-dark;
}

.contest__submit:active {
  color: $white-color-30;
  background-color: $green-color-dark;
  border: 2px solid $green-color-dark;
}

.contest__submit:disabled {
  background-color: $grey-border;
  border: 2px solid $grey-border;
}

.contest__legend {
  font-size: 14px;
  line-height: 30px;
  font-weight: 400;
}

.contest__legend sup {
  font-size: 14px;
  line-height: 30px;
  font-weight: 400;
  color: $red-color;
}
