.download {
  font-family: $font-family;
  color: $white-color;
  background-color: $download-bg-color;  
}

.download__slogan {
  display: none;

  font-size: 40px;
  font-weight: 300;
}

.download__picture {
  padding: 0 20px;
  margin: 0 auto;

  width: 280px;
  height: 462px;
  background-color: $nav-bg-color;
  background-image: url("./../img/bg-back-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: center 100%;
}

.download__image {
  margin: 0 auto;
  margin-top: -286px;

  width: 280px;
  height: 286px;

  bottom: 0;
  left: 0;

  background-image: url("./../img/iphone-hand-mobile@1x.png");
  background-repeat: no-repeat;
  background-position: center 0;
}

.download__text-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;

  width: 280px;  
  margin: 0 auto;
  padding: 0 20px;
  padding-top: 28px;
  padding-bottom: 33px;
}

.download__download-app {
  display: block;
  margin-bottom: 32px;
  padding: 15px 35px;
  font-size: 18px;
  line-height: 18px;
  font-weight: 700;

  color: $white-color;

  border: 3px solid $white-color;
  border-radius: 50px;
  text-align: center;
  text-decoration: none;
}

.download__download-app:hover {
  color: $red-color;
  background-color: $white-color;
  border: 3px solid $white-color;
  box-shadow: 0 5px 7px 0 rgba(1, 1, 1, 0.75);
}

.download__download-app:active {
  color: $red-color-30;
  background-color: $white-color;
  border: 3px solid $white-color;
}

.download__list {
  @include margin0-padding0;

  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 25px;
  padding: 0 38px;

  list-style: none;
}

.download__text {
  @include margin0-padding0;

  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
  text-align: center;
}


@media (min-width: $tablet-width) {
  .download__picture {
    width: 620px;
    min-height: 729px;
    background-image: url("./../img/bg-back-tablet@1x.jpg");
    background-size: cover;
  }

  .download__image {
    margin-top: -545px;
    overflow: hidden;
    width: 660px;
    height: 545px;

    left: -20px;

    background-image: url("./../img/iphone-hand-tablet@1x.png");
  }

  .download__text-wrapper {
    justify-content: center;
    align-items: flex-start;
    margin-top: -620px;
    padding: 0;

    width: 620px;
    min-height: 620px;
  }

  .download__slogan {
    @include margin0-padding0;

    display: block;
    margin-bottom: 30px;

    text-align: center;
  }

  .download__download-app {
    margin-bottom: 45px;
  }

  .download__list {
    margin-bottom: 38px;
  }

  .download__text-left {
    display: flex;
    flex-direction: column;

    width: 300px;
  }
}

@media (min-width: $desktop-width) {
  .download__picture {
    width: 2560px;
    min-height: 853px;

    background-image: url("./../img/bg-back-desktop@1x.jpg");
    background-size: cover;
  }

  .download__image {
    margin-top: -645px;
    overflow: hidden;
    width: 1050px;
    height: 645px;

    background-image: url("./../img/iphone-hand-desktop@1x.png");
    background-size: cover;
  }

  .download__text-wrapper {
    justify-content: flex-start; 
    width: 940px;   
  }

  .download__slogan {   
    margin-bottom: 48px;
    padding: 0 30px;
  }

  .download__download-app {
    margin-bottom: 66px;
  }

  .download__list {
    margin-bottom: 56px;
  }

}
